.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  animation: pulse 5s infinite alternate;
}

h1 {
    font-weight: 200;
    font-size: 2.5em;
    margin: 0.4em 0;
    cursor: pointer;
    animation:
            typing 3.5s steps(40, end),
            blink-caret .75s step-end;
}

h1:hover {
    animation: rainbow 5s infinite alternate;
}

section.links {
    display: flex;
    align-content: space-around;
}

section.links img {
    height: 10vmin;
    align-self: center;
    margin-left: 1.2rem;
}

section.links a {
    display: flex;
    width: 15vmin;
    height: 15vmin;
    border-radius: 50%;
    align-items: center;
}
section.links a:hover {
    background: rgba(255, 255, 255, 0.5);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes pulse {
    0% {
        background-color: #282c34;
    }
    100% {
        background-color: #000;
    }
}

/* The typing effect */
@keyframes typing {
    from { width: 0 }
    to { width: 100% }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: orange; }
}

@keyframes rainbow {
    0% {
        color: red;
    }
    10% {
        color: orange;
    }
    20% {
        color: yellow;
    }
    30% {
        color: chartreuse;
    }
    40% {
        color: green;
    }
    50% {
        color: deepskyblue;
    }
    60% {
        color: blue;
    }
    70% {
        color: darkblue;
    }
    80% {
        color: navy;
    }
    90% {
        color: blueviolet;
    }
    100% {
        color: indigo;
    }
}